<template>
  <div>
    <el-tabs :tab-position="tabPosition" style="height: 200px;">
      <el-tab-pane label="">
        <el-radio v-model="proxy" label="global"></el-radio>
        <el-radio v-model="proxy" label="pac">pac</el-radio>
        <el-radio v-model="proxy" label="off"></el-radio>
      </el-tab-pane>
      <el-tab-pane label="()">
        <el-time-select placeholder="" v-model="startTime" :picker-options="{start: '00:00', step: '00:15', end: '23:45'}">
        </el-time-select>
        <el-time-select placeholder="" v-model="endTime" :picker-options="{ start: '00:00',step: '00:15',end: '23:45'}">
        </el-time-select>
        <el-button @click="selectNode"></el-button>
        <p>{{night?night.name:''}}</p>
      </el-tab-pane>
      <el-tab-pane label="">
        <label></label>
        <el-switch v-model="login" active-color="#13ce66" inactive-color="#ff4949" @change="changeLogin" />
        <p>~</p>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import {
    calcTime
  } from '../utils/time'
  import {
    clearTimeout
  } from 'timers';
  export default {
    data() {
      return {
        login: false,
        tabPosition: 'right',
        proxy: null,
        startTime: null,
        endTime: null,
        night: null,
        pid1: null,
        pid2: null
      }
    },
    mounted() {
      let ipc = electron.ipcRenderer
      ipc.send('get-login')
      ipc.on('config', (e, conf) => {
        let {
          proxy,
          night,
          time: {
            startTime,
            endTime
          },
        } = conf
        this.proxy = proxy
        this.startTime = startTime
        this.endTime = endTime
        this.night = night
      }).on('login', (e, login) => {
        this.login = login
      }).on('mode', () => {
        ipc.send('getConf')
        if (this.$global.link) ipc.send('link')
      })
      setTimeout(() => {
        console.log('set')
        this.opneNight({
          startTime: this.startTime,
          endTime: this.endTime
        })
      }, 1000)
    },
    watch: {
      'proxy': function(newval, old) {
        if (!old) return;
        let ipc = electron.ipcRenderer
        ipc.send('setConf', {
          proxy: newval
        })
        if (this.$global.link) setTimeout(() => {
          ipc.send('link')
        }, 1000)
      },
    },
    methods: {
      changeLogin(e) {
        let ipc = electron.ipcRenderer
        ipc.send('set-login', e)
      },
      selectNode() {
        if (this.$global.pid1) {
          window.clearTimeout(this.$global.pid1)
          this.$global.pid1 = null
        }
        if (this.$global.pid2) {
          window.clearTimeout(this.$global.pid2)
          this.$global.pid2 = null
        }
        const time = {
          startTime: this.startTime,
          endTime: this.endTime
        }
        let ipc = electron.ipcRenderer
        ipc.send('setConf', {
          time
        })
        if (!this.night) {
          return this.$message('~')
        }
        const tem = this.opneNight(time)
        tem && this.$message('')
      },
      timer(mode, timeout, cb) {
        let ipc = electron.ipcRenderer
        return window.setTimeout(() => {
          ipc.send('change-mode', mode)
        }, timeout);
      },
      opneNight(time) {
        if (this.$global.pid1 || this.$global.pid2) return false;
        let ipc = electron.ipcRenderer
        const [isOpen, last, night = 0] = calcTime(time)
        if (last === 0) {
          this.$message('')
          return false
        }
        console.log(isOpen, last, night)
        this.$global.pid1 = this.timer('night', night * 1000)
        this.$global.pid2 = this.timer('day', last * 1000)
        return true
      }
    }
  }
</script>
